.wrapper {
    background: #F2F2F2;

    .container {
        display: flex;
        flex-wrap: wrap;

        .left-1 {
            width: calc(100% - 650px);
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            padding: 0 45px;

            h2 {
                font-weight: bold;
                font-size: 24px;
                text-align: center;
                text-transform: uppercase;
                color: #000000;
            }

            .registration {
                display: flex;
                flex-direction: column;
                margin-top: 75px;

                input {
                    padding: 20px 27px;
                    background: #FFFFFF;
                    outline: none;
                    font-size: 19px;
                    border: 1px solid rgba(151, 151, 151, 0.482592);
                    border-radius: 5px;
                }

                .next-btn {
                    text-align: center;
                    margin-top: 26px;
                    background: #41D481;
                    border-radius: 5px;
                    font-size: 17px;
                    padding: 20px 0;
                    display: inline-block;
                    text-decoration: none;
                    color: white;
                    margin-bottom: 50px;
                }
            }

            .to-login {
                text-align: center;
                margin-top: 20px;
                font-size: 16px;
                line-height: 20px;
                color: #4E72CA;
                text-decoration: none;
                font-weight: bold;
                padding: 0 30px;
                font-family: 'Gilroy bold', sans-serif;
            }

            .bottom {
                text-align: center;
                width: 100%;
                color: #787993;
                position: relative;

                span {
                    &::before {
                        content: '';
                        width: 100%;
                        top: 0;
                        bottom: 0;
                        width: 45%;
                        position: absolute;
                        height: 2px;
                        background: #787993;
                        left: 0;
                        margin: auto 0;
                    }

                    &::after {
                        content: '';
                        width: 100%;
                        top: 0;
                        bottom: 0;
                        width: 45%;
                        position: absolute;
                        height: 2px;
                        background: #787993;
                        right: 0;
                        margin: auto 0;
                    }
                }
            }
        }

        .right {
            width: 650px;
            height: 950px;
            background: url(../../assets/images/Login-bg.png) no-repeat;
            background-size: cover;
            position: relative;
            z-index: 1;

            .extra {
                width: 275px;
                height: 275px;
                background: url(../../assets/images/login-small.svg) no-repeat;
                background-size: cover;
                position: absolute;
                bottom: 185px;
                left: 60px;
                padding: 79px 42px 0 42px;

                p {
                    font-family: 'Gilroy bold', sans-serif;
                    font-weight: bold;
                    font-size: 19px;
                    color: #787993;
                    margin: 7px 0 18px;
                }

                a {
                    display: inline-block;
                    text-decoration: none;
                    color: #41D481;
                    padding: 9px 18px;
                    border: 2px solid #41D481;
                    border-radius: 5px;
                    font-weight: bold;
                    font-size: 16px;
                    font-family: 'Gilroy bold', sans-serif;
                }
            }
        }
    }
}